body{
  background: #14213d;
  font-family: 'Helvetica', 'arial', sans-serif; 
}
.App{
  max-width: 950px;
  margin: auto;
  
}
.boxAll{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: auto; 
  
}
.box{
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29);
  color: white;
  display: block;
  margin: 15px;
}
h1{
  text-align: center;
  color: white;
  font-weight: bold;
  letter-spacing: 3px;
}

